.container {
  margin: 0 auto;
  max-width: 75em;
  width: 100%;
  padding: 0 0.9375em;
  @include tablet {
    max-width: 60em;
    width: 100%;
  }
  @include mobile {
    max-width: 47.5em;
    width: 100%;
  }
  @include small-tablet {
    max-width: 50em;
    width: 100%;
  }
}